@media (max-width: 768px) {
  .stats {
    flex-direction: column;
    gap: 40px;
  }

  .navbar {
    padding: 15px 20px;
  }

  .overlay h1 {
    font-size: 40px;
  }
}

* {
  box-sizing: border-box;
}

.stats {
  display: flex;
  justify-content: center;
  gap: 80px;
  padding: 80px 0;
  background: #0a0a0a;
}

.stat {
  text-align: center;
}

.stat h2 {
  font-size: 60px;
  color: #ff3c00;
  font-family: 'Bebas Neue', sans-serif;
}

.stat p {
  opacity: 0.7;
  font-size: 14px;
  letter-spacing: 2px;
}

h1, h2, .logo {
  font-family: 'Bebas Neue', sans-serif;
  font-size: 34px;
  letter-spacing: 6px;
  color: #ffc300;
}

body {
  margin: 0;
  overflow-x: hidden;
  font-family: 'Orbitron', sans-serif;
  background: #000;
  color: white;
}

html {
  scroll-behavior: smooth;
}

/* NAVBAR */
.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 15px 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(0,0,0,0.6);
  backdrop-filter: blur(10px);
  z-index: 1000;
}


.nav-links a {
  color: white;
  margin-left: 20px;
  text-decoration: none;
  transition: 0.3s;
}

.nav-links a:hover {
  color: #ff3c00;
}

/* HERO */
.hero {
  animation: slowZoom 20s infinite alternate;
}

@keyframes slowZoom {
  from { background-size: 100%; }
  to { background-size: 110%; }
}

.hero {
  height: 100vh;
  position: relative;
  padding-top: 5vh;
  background:
    linear-gradient(to bottom, rgba(0,0,0,0.6), rgba(0,0,0,0.85)),
    url("../src/assets/corvette_f.jpg") center/cover no-repeat;
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

.second-bg {
  height: 100vh;
  position: relative;
  background:
    linear-gradient(to top, rgba(0,0,0,0.8), rgba(0,0,0,0.6)),
    url("./assets/corvette.jpg") center/cover no-repeat;
}

.second-bg::before {
  content: "";
  position: absolute;
  top: -100px;
  width: 100%;
  height: 100px;
  background: linear-gradient(to bottom, transparent, black);
}

.overlay {
  padding: 60px;
  text-align: center;
  color: white;
  border-radius: 12px;
  animation: fadeIn 1.5s ease;
}

.overlay h1 {
  font-size: 75px;
  text-shadow: 0 5px 30px rgba(0,0,0,0.9);
  letter-spacing: 5px;
  font-weight: 700;
}

.overlay p {
  font-size: 25px;
  text-shadow: 0 2px 15px rgba(0,0,0,0.8);
  opacity: 0.85;
}

button {
  padding: 12px 30px;
  background: transparent;
  border: 2px solid #ff3c00;
  color: white;
  cursor: pointer;
  margin-top: 20px;
  transition: 0.4s;
  letter-spacing: 2px;
}

button:hover {
  background: #ff3c00;
}

/* SECTIONS */
.section {
  padding: 100px 40px;
  text-align: center;
}

.dark {
  background: #111;
}

/* GRID */
.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
  margin-top: 40px;
}

.card {
  background: #1a1a1a;
  padding: 30px;
  border-radius: 12px;
  transition: 0.4s ease;
  border: 1px solid rgba(255,255,255,0.05);
}

.card:hover {
  transform: translateY(-10px);
  background: #242424;
  border-color: #ff3c00;
}

.card h3 {
  margin-bottom: 10px;
  color: #ff3c00;
}


/* ANIMATION */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}
